<script>
import { getResult } from "@/assets/calc/calcfunctions.js";

export default {
  data() {
    return {
      places: [
        {
          id: 1,
          name: "",
          type: "basic",
          molding: "none",
          width: "",
          height: "",
          result: "결과",
        },
      ],
      place: {
        id: -1,
        name: "",
        type: "basic",
        molding: "none",
        width: "",
        height: "",
        result: "",
      },
      moldingTypes: [
        {
          text: "몰딩 없음",
          value: "none",
        },
        {
          text: "위쪽 혹은 아래쪽",
          value: "upOrDown",
        },
        {
          text: "왼쪽 혹은 오른쪽",
          value: "leftOrRight",
        },
        {
          text: "위쪽 아래쪽 둘 다",
          value: "upAndDown",
        },
        {
          text: "왼쪽 오른쪽 둘 다",
          value: "leftAndRight",
        },
      ],
    };
  },

  methods: {
    deletePlace(place) {
      if (confirm(place.name + "을 목록에서 삭제 하시겠습니까?")) {
        console.log("remove");
      }
    },
    calculate(place) {
      let result = "";
      let height = place.height;
      let width = place.width;
      console.log(place.molding);
      switch (place.molding) {
        case "upOrDown":
          height -= 18;
          break;
        case "leftOrRight":
          width -= 18;
          break;
        case "upAndDown":
          height -= 36;
          break;
        case "leftAndRight":
          width -= 36;
          break;
      }
      let isPlus = height % 2440;
      if (isPlus != 0) {
        height = parseInt(height) + 3;
      }
      if (place.type === "wide") {
        result = getResult(width, height, true);
      } else {
        result = getResult(width, height, false);
      }
      place.result = result;
    },
  },

  computed: {
    height() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return 120;
        case "sm":
          return 120;
        case "md":
          return 150;
        case "lg":
          return 150;
        case "xl":
          return 180;
      }
      return 200;
    },
  },
};
</script>

