const original = {
    width: 100,
    height: 2440,
};

const wide = {
    width: 120,
    height: 2440,
};

function getResult(width, height, isWide) {
    console.log();
    if (height > 4880) {
        return "010-3238-7142로 연락 부탁드립니다 :)";
    }

    let standardWidth = original.width;
    let standardHeight = 2442;
    let productName = "베이직, 라운드";

    if (isWide) {
        standardWidth = wide.width;
        productName = "와이드";
    }

    // 높이가 2440mm 가 넘어갈 경우
    if (height > 2440) {
        let count = 0;
        let base = Math.ceil(width / standardWidth);
        count += base;

        let restHeight = height - standardHeight;
        let countFromOneBoard = Math.floor(standardHeight / restHeight);

        let rest = Math.ceil(base / countFromOneBoard);

        count += rest;

        return productName + " " + count + "개";
    } else if (height > 1220) {
        let count = 0;
        let base = Math.ceil(width / standardWidth);
        count += base;
        console.log("over 1220 : " + count);

        return productName + " " + count + "개";
    } else {
        let count = 0;
        let base = Math.ceil(width / standardWidth);

        let restHeight = height;
        let countFromOneBoard = Math.floor(standardHeight / restHeight);
        console.log("count : " + countFromOneBoard);

        let rest = Math.ceil(base / countFromOneBoard);

        count += rest;

        return productName + " " + count + "개";
    }
}

export { getResult };